<template>
  <div class="lecturer-info-container flex-start align-start">
    <!-- <Avatar
      :imgUrl="require('@/assets/mock/avatar_test.png')"
      :width="160"
      :height="215"
    /> -->
    <div class="avatar-container" 
          @click="goToInformationChange">
      <Avatar
        :imgUrl="
          lecturerInfo.img
            ? lecturerInfo.img
            : 'https://static.familyeducation.org.cn/fsa/20220801/4b731779096b53ff052a3237ea8888e4.png'
        "
        :width="160"
        :height="215"
      />
      <div class="edit-icon">
        <img src="@/assets/icon/icon-edit-blue.png" alt="" srcset="">
      </div>
    </div>
    <div class="lecturer-info-right">
      <div class="flex">
        <div class="flex-start">
          <p class="lecturer-info-username">{{ lecturerInfo.name }}</p>
          <IdentityTag v-if="mtidsName" :title="mtidsName" />
        </div>
        <van-button
          @click="doLogout"
          class="editor-btn-style"
          size="small"
          plain
          round
        >
          退出登录
        </van-button>
        <!-- <van-button
          @click="goToInformationChange"
          class="editor-btn-style"
          size="small"
          round
        >
          <van-image
            class="btn-icon-style"
            :src="require('@/assets/icon/editor.png')"
          ></van-image>
          编辑资料
        </van-button> -->
      </div>
      <div @click="goToInformationChange" class="edit-container">
        <img src="@/assets/icon/icon-edit.png" alt="" />
        点击编辑资料
      </div>
      <ul class="lecturer-data-list flex-start">
        <li
          v-if="$store.getters.associationInfo.model_type !== 'district'"
          class="lecturer-data-item"
        >
          <p class="item-value">{{ lecturerInfo.area_count }}</p>
          <p class="item-label">巡讲区县</p>
        </li>
        <li class="lecturer-data-item">
          <p class="item-value">{{ lecturerInfo.lecture_count }}</p>
          <p class="item-label">活动场次</p>
        </li>
        <li class="lecturer-data-item">
          <p class="item-value">{{ lecturerInfo.sum_visitor }}</p>
          <p class="item-label">学员人数</p>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  components: {
    Avatar: () => import("@/components/Avatar"),
    IdentityTag: () => import("@/components/IdentityTag"),
  },
  data() {
    return {
      // 讲师信息
      instructorInfoGet: null,
      // 身份标签文字
      mtidsName: "",
    };
  },
  computed: {
    lecturerInfo() {
      return this.$store.state.lecturer.lecturerInfo;
    },
  },
  async created() {
    const iid = this.$store.getters.accountIdInfo.iid;
    const aid = this.$store.getters.associationInfo.id;
    if (iid && aid) {
      this.instructorInfoGet = await this.instructorInfoGetAPI(aid, iid);
      const instructorInfoGetMtid = this.instructorInfoGet.mtid;
      console.log("instructorInfoGetMtid :>> ", instructorInfoGetMtid);
      if (instructorInfoGetMtid) {
        this.instructorTitleGetAPI(`${instructorInfoGetMtid}`);
      }
    }
  },
  methods: {
    doLogout(){
      this.$dialog
        .confirm({
          title: "",
          message: "确定退出登录？",
          confirmButtonColor: "#2374FF",
          cancelButtonColor: "#999999",
        })
        .then(() => {
          this.$store.commit("user/doLogout");
          this.$router.replace('/')
        })
        .catch(() => {
          console.log("取消删除 :>>");
        });
    },
    goToInformationChange() {
      const lecturerId = this.$store.getters.lecturerInfo.id;
      this.$router.push({
        path: "/speakerInformationChanges",
        query: {
          iid: lecturerId,
        },
      });
    },
    /**
     * 通过讲师ID查询职称ID
     * @param {String} aid 机构ID
     * @param {String} iid 导师ID
     * @returns {Object} 讲师数据对象
     */
    async instructorInfoGetAPI(aid, iid) {
      const data = {
        aid,
        id: iid,
      };
      return await this.$request("instructor.instructorInfoGet", data);
    },

    /**
     * 通过ids获取职称
     * @param {Array} mtid 身份标签ID
     */
    async instructorTitleGetAPI(mtid) {
      const data = {
        mtid,
      };
      const res = await this.$request("instructor.instructorTitleGet", data);
      console.log("res :>> ", res);
      this.mtidsName = res.name;
    },
  },
};
</script>

<style lang="scss" scoped>
.lecturer-info-container {
  .avatar-container {
    position: relative;

    .edit-icon{
      position: absolute;
      bottom: 0;
      right: 0;
      img{
        width: px(32);
      }
    }
  }

  .lecturer-info-right {
    margin-left: px(30);
    .lecturer-info-username {
      width: px(160);
      font-size: px(40);
      font-weight: bold;
      @include text-ellipsis(1);
    }

    .editor-btn-style {
      background: transparent;
      border: 1px solid #999999;
      color: #999999;
      padding: 0 px(24);
      font-size: px(26);
      .btn-icon-style {
        margin-right: px(8);
        width: px(20);
        height: px(20);
      }
    }

    .edit-container {
      display: flex;
      align-items: center;
      font-size: px(26);
      color: #999999;

      img {
        width: px(20);
        margin-right: px(10);
      }
    }
    .lecturer-data-list {
      margin-top: px(70);
      .lecturer-data-item {
        margin-right: px(75);
        .item-label {
          width: px(100);
          font-size: px(24);
          color: #cccccc;
        }
        .item-value {
          font-size: px(36);
          font-weight: bold;
        }
      }
    }
  }
}
</style>
